<template>
    <div id="employee">
        <h3>Novo Colaborador</h3>
        <div class="row">
            <form @submit.prevent="saveEmployee" class="col s12">
            
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="employee_id" required placeholder="ID#">
                </div>
            </div>
            
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="name" required placeholder="Nome">
                </div>
            </div>
            
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="dept" required placeholder="Departamento">
                </div>
            </div>
            
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="position" required placeholder="Cargo">  
                </div>
            </div>
            
            <button type=submit class="btn">
                INSERIR
            </button>
            
            <router-link to="/" class="btn grey"> CANCELAR</router-link>
            </form>
        </div>
    </div>
</template>
<script>
    import db from './firebaseInit'
    export default {
        name: 'new-employee',
        data() {
            return {
                employee_id: null,
                name: null,
                dept: null,
                position: null
            }
        },
        methods: {
            saveEmployee() {
                db.collection('employees').add({
                        employee_id: this.employee_id,
                        name: this.name,
                        dept: this.dept,
                        position: this.position
                    })
                    .then(docRef => this.$router.push('/'))
                    .catch(error => console.log(err))
            }
        }
    }

</script>



